<template><div class="markdown-body"><h1>提示框</h1>
<p><code v-pre="">mu-alert</code> 组件用来向用户传递信息，它可以定义不同的颜色和图标。</p>
<h2 id="ji-chu-shi-li"><a class="header-anchor" href="#ji-chu-shi-li" aria-hidden="true">¶</a> 基础示例</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;div class=\&quot;alert-demo-wrapper\&quot;&gt;\n  &lt;mu-alert color=\&quot;success\&quot;&gt;\n    this is success alert\n  &lt;/mu-alert&gt;\n  &lt;mu-alert color=\&quot;warning\&quot;&gt;\n    this is warning alert\n  &lt;/mu-alert&gt;\n  &lt;mu-alert color=\&quot;info\&quot;&gt;\n    this is info alert\n  &lt;/mu-alert&gt;\n  &lt;mu-alert color=\&quot;error\&quot;&gt;\n    this is error alert\n  &lt;/mu-alert&gt;\n&lt;/div&gt;\n\n&quot;,&quot;script&quot;:null,&quot;style&quot;:&quot;\n.alert-demo-wrapper {\n  width: 100%;\n  &gt; .mu-alert {\n    margin-bottom: 16px;\n    &amp;:last-child {\n      margin-bottom: 0;\n    }\n  }\n}\n&quot;}">
                    <div class="source" slot="source"><div class="alert-demo-wrapper">
  <mu-alert color="success">
    this is success alert
  </mu-alert>
  <mu-alert color="warning">
    this is warning alert
  </mu-alert>
  <mu-alert color="info">
    this is info alert
  </mu-alert>
  <mu-alert color="error">
    this is error alert
  </mu-alert>
</div>

</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"alert-demo-wrapper"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-alert</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"success"</span>&gt;</span>
    this is success alert
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-alert</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-alert</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"warning"</span>&gt;</span>
    this is warning alert
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-alert</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-alert</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"info"</span> &gt;</span>
    this is info alert
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-alert</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-alert</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"error"</span> &gt;</span>
    this is error alert
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-alert</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span> <span class="hljs-attr">lang</span>=<span class="hljs-string">"less"</span>&gt;</span><span class="undefined">
.alert-demo-wrapper {
  width: 100%;
  &gt; .mu-alert {
    margin-bottom: 16px;
    &amp;:last-child {
      margin-bottom: 0;
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="xian-shi-icon-he-delete-tu-biao"><a class="header-anchor" href="#xian-shi-icon-he-delete-tu-biao" aria-hidden="true">¶</a> 显示 Icon 和 delete 图标</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;div class=\&quot;alert-demo-wrapper\&quot;&gt;\n  &lt;mu-alert color=\&quot;success\&quot;&gt;\n    &lt;mu-icon value=\&quot;check_circle\&quot;&gt;&lt;/mu-icon&gt; this is success alert\n  &lt;/mu-alert&gt;\n  &lt;mu-alert color=\&quot;warning\&quot;&gt;\n    &lt;mu-icon value=\&quot;priority_high\&quot;&gt;&lt;/mu-icon&gt; this is warning alert\n  &lt;/mu-alert&gt;\n  &lt;mu-alert color=\&quot;info\&quot;&gt;\n    &lt;mu-icon value=\&quot;info\&quot;&gt;&lt;/mu-icon&gt; this is info alert\n  &lt;/mu-alert&gt;\n  &lt;mu-alert color=\&quot;error\&quot; delete :show.sync=\&quot;alert\&quot;&gt;\n    &lt;mu-icon value=\&quot;warning\&quot;&gt;&lt;/mu-icon&gt; this is error alert\n  &lt;/mu-alert&gt;\n  &lt;mu-button color=\&quot;primary\&quot; v-if=\&quot;!alert\&quot; @click=\&quot;closeAlert()\&quot;&gt;SHOW ALERT&lt;/mu-button&gt;\n&lt;/div&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      alert: true\n    };\n  },\n  methods: {\n    closeAlert () {\n      this.alert = true;\n    }\n  }\n}\n&quot;,&quot;style&quot;:&quot;\n.alert-demo-wrapper {\n  width: 100%;\n  &gt; .mu-alert {\n    margin-bottom: 16px;\n    &amp;:last-child {\n      margin-bottom: 0;\n    }\n  }\n  &gt; .mu-button {\n    margin: 0 auto;\n    display: block;\n  }\n}\n&quot;}">
                    <div class="source" slot="source"><div class="alert-demo-wrapper">
  <mu-alert color="success">
    <mu-icon value="check_circle"></mu-icon> this is success alert
  </mu-alert>
  <mu-alert color="warning">
    <mu-icon value="priority_high"></mu-icon> this is warning alert
  </mu-alert>
  <mu-alert color="info">
    <mu-icon value="info"></mu-icon> this is info alert
  </mu-alert>
  <mu-alert color="error" delete="" :show.sync="alert">
    <mu-icon value="warning"></mu-icon> this is error alert
  </mu-alert>
  <mu-button color="primary" v-if="!alert" @click="closeAlert()">SHOW ALERT</mu-button>
</div>


</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"alert-demo-wrapper"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-alert</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"success"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"check_circle"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span> this is success alert
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-alert</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-alert</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"warning"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"priority_high"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span> this is warning alert
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-alert</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-alert</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"info"</span> &gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"info"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span> this is info alert
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-alert</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-alert</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"error"</span> <span class="hljs-attr">delete</span> <span class="hljs-attr">:show.sync</span>=<span class="hljs-string">"alert"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"warning"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span> this is error alert
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-alert</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">v-if</span>=<span class="hljs-string">"!alert"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"closeAlert()"</span>&gt;</span>SHOW ALERT<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">alert</span>: <span class="hljs-literal">true</span>
    };
  },
  <span class="hljs-attr">methods</span>: {
    closeAlert () {
      <span class="hljs-keyword">this</span>.alert = <span class="hljs-literal">true</span>;
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span> <span class="hljs-attr">lang</span>=<span class="hljs-string">"less"</span>&gt;</span><span class="undefined">
.alert-demo-wrapper {
  width: 100%;
  &gt; .mu-alert {
    margin-bottom: 16px;
    &amp;:last-child {
      margin-bottom: 0;
    }
  }
  &gt; .mu-button {
    margin: 0 auto;
    display: block;
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="guo-du-dong-hua-ding-zhi"><a class="header-anchor" href="#guo-du-dong-hua-ding-zhi" aria-hidden="true">¶</a> 过渡动画定制</h2>
<p>通过 <code v-pre="">transition</code> 属性定制过渡动画。</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;div class=\&quot;alert-demo-wrapper\&quot;&gt;\n  &lt;mu-button color=\&quot;primary\&quot; style=\&quot;margin-bottom: 16px;\&quot; @click=\&quot;toggleAlert()\&quot;&gt;TOGGLE&lt;/mu-button&gt;\n  &lt;mu-alert color=\&quot;warning\&quot; delete :show.sync=\&quot;alert1\&quot; transition=\&quot;mu-scale-transition\&quot;&gt;\n    &lt;mu-icon value=\&quot;warning\&quot;&gt;&lt;/mu-icon&gt; this is warning alert\n  &lt;/mu-alert&gt;\n&lt;/div&gt;\n\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      alert1: true\n    };\n  },\n  methods: {\n    toggleAlert () {\n      this.alert1 = !this.alert1;\n    }\n  }\n}\n&quot;,&quot;style&quot;:&quot;\n.alert-demo-wrapper {\n  width: 100%;\n  &gt; .mu-alert {\n    margin-bottom: 16px;\n    &amp;:last-child {\n      margin-bottom: 0;\n    }\n  }\n  &gt; .mu-button {\n    margin: 0 auto;\n    display: block;\n  }\n}\n\n.mu-scale-transition-enter-active,\n.mu-scale-transition-leave-active {\n  transition: transform .45s cubic-bezier(0.23, 1, 0.32, 1), opacity .45s cubic-bezier(0.23, 1, 0.32, 1);\n  backface-visibility: hidden;\n}\n\n.mu-scale-transition-enter,\n.mu-scale-transition-leave-active {\n  transform: scale(0);\n  opacity: 0;\n}\n&quot;}">
                    <div class="source" slot="source"><div class="alert-demo-wrapper">
  <mu-button color="primary" style="margin-bottom: 16px;" @click="toggleAlert()">TOGGLE</mu-button>
  <mu-alert color="warning" delete="" :show.sync="alert1" transition="mu-scale-transition">
    <mu-icon value="warning"></mu-icon> this is warning alert
  </mu-alert>
</div>



</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"alert-demo-wrapper"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"margin-bottom: 16px;"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"toggleAlert()"</span>&gt;</span>TOGGLE<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-alert</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"warning"</span> <span class="hljs-attr">delete</span> <span class="hljs-attr">:show.sync</span>=<span class="hljs-string">"alert1"</span> <span class="hljs-attr">transition</span>=<span class="hljs-string">"mu-scale-transition"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"warning"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span> this is warning alert
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-alert</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">alert1</span>: <span class="hljs-literal">true</span>
    };
  },
  <span class="hljs-attr">methods</span>: {
    toggleAlert () {
      <span class="hljs-keyword">this</span>.alert1 = !<span class="hljs-keyword">this</span>.alert1;
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span> <span class="hljs-attr">lang</span>=<span class="hljs-string">"less"</span>&gt;</span><span class="undefined">
.alert-demo-wrapper {
  width: 100%;
  &gt; .mu-alert {
    margin-bottom: 16px;
    &amp;:last-child {
      margin-bottom: 0;
    }
  }
  &gt; .mu-button {
    margin: 0 auto;
    display: block;
  }
}

.mu-scale-transition-enter-active,
.mu-scale-transition-leave-active {
  transition: transform .45s cubic-bezier(0.23, 1, 0.32, 1), opacity .45s cubic-bezier(0.23, 1, 0.32, 1);
  backface-visibility: hidden;
}

.mu-scale-transition-enter,
.mu-scale-transition-leave-active {
  transform: scale(0);
  opacity: 0;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>

</code></pre>
</div></demo-block>
<h2 id="alert-props"><a class="header-anchor" href="#alert-props" aria-hidden="true">¶</a> Alert Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>color</td>
<td>组件颜色</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>delete</td>
<td>是否显示删除图标</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>show</td>
<td>组件是否显示, 使用 <code v-pre="">.sync</code> 修饰符双向绑定</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>transition</td>
<td>设置组件显示和隐藏的过渡动画</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>mode</td>
<td>设置过渡动画的模式</td>
<td>String</td>
<td>out-in/in-out</td>
<td>—</td>
</tr>
</tbody>
</table>
<h2 id="alert-events"><a class="header-anchor" href="#alert-events" aria-hidden="true">¶</a> Alert Events</h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>介绍</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>delete</td>
<td>当删除图标被点击时触发</td>
<td>—</td>
</tr>
</tbody>
</table>


</div class="markdown-body"></template>
<style lang="less">
.alert-demo-wrapper {
  width: 100%;
  > .mu-alert {
    margin-bottom: 16px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  > .mu-button {
    margin: 0 auto;
    display: block;
  }
}

.mu-scale-transition-enter-active,
.mu-scale-transition-leave-active {
  transition: transform .45s cubic-bezier(0.23, 1, 0.32, 1), opacity .45s cubic-bezier(0.23, 1, 0.32, 1);
  backface-visibility: hidden;
}

.mu-scale-transition-enter,
.mu-scale-transition-leave-active {
  transform: scale(0);
  opacity: 0;
}
</style>
<script>
export default {
  data () {
    return {
      alert: true,
      alert1: true
    };
  },
  methods: {
    closeAlert () {
      this.alert = true;
    },
    toggleAlert () {
      this.alert1 = !this.alert1;
    }
  }
}
</script>


